.home-wrapper {
  height: 100%;
}
.home-wrapper .vux-swiper-desc {
  display: none;
}
.home-wrapper .nav {
  height: 0.72rem;
  line-height: 0.72rem;
  font-size: 0.24rem;
  color: #666666;
  padding: 0 0.2rem;
  background: #fff;
}
.home-wrapper .nav .iconfont {
  color: #24a5ed;
  vertical-align: -0.08em;
}
.home-wrapper .wrapper {
  background: #fff;
  margin-top: 0.2rem;
  padding: 0.2rem;
}
.home-wrapper .wrapper h3 {
  font-size: 0.32rem;
  margin-bottom: .2rem;
}
.home-wrapper .wrapper .item {
  margin-bottom: 0.2rem;
  border: 1px solid #f5f5f5;
  -webkit-box-shadow: 0 0.05rem 0.24rem 0 rgba(0, 0, 0, 0.05);
          box-shadow: 0 0.05rem 0.24rem 0 rgba(0, 0, 0, 0.05);
  border-radius: 0.08rem;
}
.home-wrapper .wrapper .item .content {
  padding: 0.2rem;
}
.home-wrapper .wrapper .item .group-label {
  color: #fff;
  line-height: 0.32rem;
  font-size: 0.22rem;
}
.home-wrapper .wrapper .item .group-label span {
  display: inline-block;
  background: #24a5ed;
  margin-right: 0.12rem;
  padding: 0 0.2rem;
  border-radius: 0.08rem;
}
.home-wrapper .wrapper .item .footer {
  line-height: .72rem;
  height: .72rem;
  padding: 0 .2rem;
  color: #666;
  font-size: .24rem;
  border-top: 1px solid #f2f2f2;
}
.home-wrapper .wrapper .item .footer .iconfont {
  color: #24a5ed;
  vertical-align: -0.04rem;
}
.home-wrapper .wrapper .item .footer .price {
  float: right;
  font-size: .32rem;
  color: #FF4A4A;
